@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{
  background-color: lightGray;
  box-sizing: border-box;

  
}

h1 {
  font-family: 'IBM Plex Mono', monospace;
  text-align: center;
  font-size: 3em;
}
#music-notes {
  font-size: 0.75em;
}

@media only screen and (max-width: 750px){

      h1{
        font-size: 2.5em;
        margin-bottom: -2em;
        margin-top: 0;
      }


}

@media only screen and (max-width: 280px){
    
  h1{
    font-size: 2em;
    margin-bottom: -3em
  }

}




#drum-machine{
    margin: auto;
    background-color: black;
    height: 400px;
    width: 740px;
    position: relative;
    top: 0;
    bottom: 4em;
    border: 6px solid #00e600;
    display: flex;
    align-items: center;
    justify-content: space-around;
    
  }

  @media only screen and (max-width: 750px){

    body {
      display: flex;
      flex-direction: row;
      justify-content: center;
    }
    
    #drum-machine{
      
      top: 0;
      bottom: 0;
      min-width: auto;
      min-height: auto;
      height: 700px;
      width: 400px;
      -webkit-transform: scale(0.7);
              transform: scale(0.7);
      flex-direction: column;
      align-items: center;
      justify-content: space-between;

    }

  }

  @media only screen and (max-width: 280px){

  #drum-machine{
    padding: 0;
    height: -webkit-min-content;
    height: min-content;
  }

}

.drum-pad {
  box-sizing: content-box;
  border-radius: 15px;
  font-size: 28px;
  background-color: gray;
  font-family: Courier New;
  font-weight: bold;
  cursor: pointer;
  border-bottom: solid 3px #505050;
  border-right: solid 3px #404040;
}

.drum-pad:active {
  border: solid 1px black;
  }
  
  /*.drum-pad:focus {
    background-color: #00e600;
    outline: none;
  }*/

  #button-container {
    background-color: darkGray;
    width: 280px;
    height: 280px;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
    padding: 5px;
    border-radius: 15px;
  }

  @media only screen and (max-width: 750px){
    #button-container {
      top: 0;
      left: 0;
        margin-top: 10%;

    }


  }

  @media only screen and (max-width: 280px){

  #button-container{
    margin: 15px;
  }

}
  
#controls{
    height: 260px;
    width: 280px;
    background-color: darkGray;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr 1fr;
    grid-gap: 7px;
    font-weight: bold;
    text-align: center;
    padding: 15px;
  }

  @media only screen and (max-width: 750px)  {

    #controls{
        margin-bottom: 10%;
    }

  }

  @media only screen and (max-width: 280px){

    #controls{
      margin: 0 5px 15px 5px;
      width: 16em;
    }

  }

#power-button {
  border-radius: 50%;
  padding: 8px;
  background-color: black;
  font-size: 23px;
  float: right;
  cursor: pointer;
}


  button.power-on {
    color: #0099ff;
    border: 2px solid gray;
  }
  button.power-off{
    color: white;
    border: 2px solid black;
    
  }
#volume {
    padding: 15px;
  }

  #volume-switch-label {
    font-family: 'IBM Plex Mono', monospace;
    font-size: 18px;
  }

#display {
    background-color: gray;
    border: 2px solid black;
    display: inline-block;
    width: 80%;
    height: 67px;
    margin: 0 auto;
    
  }

  p {
    font-family: 'IBM Plex Mono', monospace;
    font-size: 20px;
  }
#bank {
    padding: 15px;
  }
  
  #bank-switch-holder {
    border: 2px solid black;
    background-color: black;
    display: inline-block;
    height: 20px;
    width: 50px;
    cursor: pointer;
    border-radius: 14px;
  }
  
  #bank-switch{
    cursor: pointer;
    display: block;
    background-color: red;
    height: 100%;
    width: 50%;
    border-radius: 10px;
    
  }

  .switched {
    position: relative;
    left: 50%;
  }
