
#power-button {
  border-radius: 50%;
  padding: 8px;
  background-color: black;
  font-size: 23px;
  float: right;
  cursor: pointer;
}


  button.power-on {
    color: #0099ff;
    border: 2px solid gray;
  }
  button.power-off{
    color: white;
    border: 2px solid black;
    
  }